form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

input {
  width: 70%;
  height: 30px;
  margin-bottom: 20px;
  border-radius: 10px;
  box-shadow: inset 2px 2px 5px 0px lightgrey;
}

button {
  /* margin-left: 10px; */
  width: 80px;
  height: 30px;
  border-radius: 4px;
  border: rgba(114, 123, 206, 0.5);
  font-size: 12px;
  background-color: rgba(114, 123, 206, 0.5);
  /* background-image: linear-gradient(#076f86, yellow, #928dab); */
  /* box-shadow: 0 2px 4px 2px rgba(0, 0, 0, 0.25); */
  box-shadow: 0 1px 5px 1px #ffffff;

  transition: transform 100ms linear, background-color 100ms linear,
    box-shadow 100ms linear, color 100ms linear;
}
button:hover {
  transform: scale(1.1);
  /* background-image: linear-gradient(blue); */
  background-color: blue;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3),
    -23px 0 20px -23px rgba(0, 0, 0, 0.6), 23px 0 20px -23px rgba(0, 0, 0, 0.6),
    inset 0 0 40px rgba(0, 0, 0, 0.1);
  border: 0px solid blue;
  color: #ffffff;
}
